// axios
const service = axios.create({
    baseURL: process.env.BASE_API + '/taslyb2bappmobile/b2b', // apibase_url
    timeout: 15000 // 
});

// respone
service.interceptors.response.use(
    response => {
        // TODO 
        // code0
        const res = response.data;
        alert(res.message)
        this.$vux.toast.text('hello', 'top')
        if (res.resCode != 200) {
            // Msg({
            //     title:'',
            //     description:errorCodeMap(res.resCode,res.message),
            //     icon:'warn'
            // });
            return Promise.reject('error')
        }
        return response.data
    },
    error => {
        // TODO 
        Msg({
            title:'',
            description: error.message,
            icon:'warn'
        })
        return Promise.reject(error)
    }
);
export default service


